import { withKnobs } from '@storybook/addon-knobs'
import styled, { keyframes, css} from "styled-components";

type ProgressbarType = {
    /** 메시지 */
    time: number;
    /** 버튼 이벤트 핸들러 */
    max: number;
    /** 취소 버튼 명 */
    cancel?: string;
    /** 확인 버튼 명 */
    confirm?: string;
    /** ref */
    ref?: any
}

export const ProgressBar = (props: ProgressbarType) => {
    return (
        <div className='progress-wrap'>
            <progress className='progress-bar' value={props.time} max={props.max}/>
            <span className='progress-timer'>{`${props.time}초`}</span>
        </div>
    )
};export default {
    component: ProgressBar,
    title: 'Comp/ProgressBar',
    decorators: [withKnobs]
    
}

Alert.args = {
    message: '두더지를 잡아볼까요?',
    confirm: '확인',
    cancel: '취소',
    closeHandler: () => {}
}



